import { FormControl, FormControlLabel, RadioGroup, Radio, FormLabel, Button, TextField, Typography } from "@material-ui/core";
import React from "react";
import "./TextSliderPanel.css";
import CustomSlider from './Slider';


class RadioSliderPanel extends React.Component {
    constructor(props) {
      super(props);

      var accept_checked = props.choice === "accept" ? true : false
      var reject_checked = props.choice === "reject" ? true : false
      this.state = {
        inputQuestion: props.inputQuestiono,
        inputAnswer: props.inputAnswer,
        accept_checked: accept_checked,
        reject_checked: reject_checked,
        sliderValue: props.sliderValue,
        teammateSliderValue: props.teammateSliderHandler,
        ownSliderValue: props.ownSliderHandler,
        errorMessage: props.errorMessage,
        choice: props.choice,
        howChoice: props.howChoice
      };

      this.radioHandler = props.radioHandler
      this.howRadioHandler = props.howRadioHandler
      this.sliderHandler = props.sliderHandler
      this.teammateSliderHandler = props.teammateSliderHandler
      this.ownSliderHandler = props.ownSliderHandler
      this.otherJustificationHandler = props.otherJustificationHandler
    }

    static getDerivedStateFromProps(nextProps, prevState){
      var reject_checked = nextProps.choice === "reject" ? true : false
      var accept_checked = nextProps.choice === "accept" ? true : false
      console.log(accept_checked, accept_checked)
      console.log("nextProps.choice: ", nextProps.choice)
      return {inputQuestion: nextProps.inputQuestion,
              inputAnswer: nextProps.inputAnswer,
                accept_checked: accept_checked,
                reject_checked: reject_checked,
                choice: nextProps.choice,
                howChoice: nextProps.howChoice,
                sliderValue: nextProps.sliderValue,
                teammateSliderValue: nextProps.teammateSliderValue,
                ownSliderValue: nextProps.ownSliderValue,
                errorMessage: nextProps.errorMessage}
    }

    render() {
        console.log("choice: ", this.state.choice)

        var marks = [
            {
              value: 50,
              label: 'not confident \nat all',
            },
            {
              value: 75,
              label: 'somewhat \nconfident',
            },
            {
              value: 100,
              label: 'very \nconfident',
            },
          ];

        var relyMarks = [
            {
              value: 50,
              label: "No"
            },
            {
              value: 75,
              label: "",
            },
            {
              value: 100,
              label: "Yes",
            },
          ];

        var convincingMarks = [
            {
              value: 50,
              label: 'not convincing',
            },
            {
              value: 75,
              label: 'somewhat',
            },
            {
              value: 100,
              label: 'very convincing',
            },
          ];

        var formContent;
        formContent = <div><div>
            <FormControlLabel 
                value="accept" 
                control={<Radio />} 
                label={<Typography style={{fontSize: '18px'}}>Accept</Typography>} 
                checked={this.state.choice === "accept" ? true : false}
            />
            </div>
            <div>
            <FormControlLabel 
                value="reject" 
                control={<Radio />} 
                label={<Typography style={{fontSize: '18px'}}>Reject</Typography>} 
                checked={this.state.choice === "reject" ? true : false}
            />
            </div>
        </div>

        var knowledgeContent;
        knowledgeContent = <div style={{ display: 'flex', justifyContent: 'space-between' }}>
            <div style={{width: "100%", float: "right", align:"center", border: '1px solid black',  marginLeft: "30px", marginRight: "30px", borderRadius: '10px'}}>
            <Typography style={{marginLeft: "100px", marginRight: "100px", align: "center", fontSize: '18px'}}>Do you know the correct answer?</Typography>
            <div class="slider" style={{marginBottom: "100px"}}>
                    <CustomSlider
                        className="customSlider"
                        aria-label="Reliance" 
                        value={this.state.ownSliderValue} 
                        onChange={this.ownSliderHandler} 
                        marks={relyMarks}
                        min={50}
                        max={100}
                        valueLabelDisplay="off"
                    />
                </div>
            </div>
            <div style={{width: "100%", float: "left", align:"center", border: '1px solid black', marginLeft: "30px", marginRight: "30px", borderRadius: '10px'}}>
            <Typography style={{marginLeft: "100px", marginRight: "100px", fontSize: '18px', align:"center"}}> How convincing was Teammate's answer?</Typography>
            <div class="slider" style={{marginBottom: "100px", marginLeft: "30px", marginRight: "30px"}}>
                    <CustomSlider
                        className="customSlider"
                        aria-label="Reliance" 
                        value={this.state.teammateSliderValue} 
                        onChange={this.teammateSliderHandler} 
                        marks={convincingMarks}
                        min={50}
                        max={100}
                        valueLabelDisplay="off"
                    />
                </div>
            </div>
        </div>
        
        
        return (
            <div>
                <div style={{marginBottom: "30px"}}>
                <Typography variant = "h6" align="left"><b>Part 1:</b> Would you accept the following answer? </Typography>
                </div>
                <div>
                    <div style={{ backgroundColor: 'lightblue', borderRadius: '10px', width:"70%"}}>
                    <Typography variant="h6" align="left">
                        <b> Question: </b> <i>{this.state.inputQuestion}</i>
                        <br/>
                        <b> Teammate's Answer: </b> <i>{this.state.inputAnswer}</i>
                    </Typography> 
                    </div>
                    <div style={{width: "50%"}}>
                    <FormControl>
                        <FormLabel id="demo-radio-buttons-group-label"> </FormLabel>
                        <RadioGroup
                            aria-labelledby="demo-radio-buttons-group-label"
                            name="radio-buttons-group"
                            onChange = {(event) => this.radioHandler(event)}
                            default={this.state.choice}
                            // TODO: Link this so that it resets for each example
                        >  
                        {formContent} 
                            
                        </RadioGroup>
                    </FormControl>
                    </div>
                </div>
                <div style={{marginTop: "30px", marginLeft: "0px"}}>
                <Typography variant='h6'><b>Part 2:</b> How confident are you in your decision to accept/reject? </Typography>
                </div>
                <div class="slider" style={{marginBottom: "100px"}}>
                    <CustomSlider
                        className="customSlider"
                        aria-label="Confidence" 
                        value={this.state.sliderValue} 
                        onChange={this.sliderHandler} 
                        marks={marks}
                        min={50}
                        max={100}
                        valueLabelDisplay="off"
                    />
                </div>
                <div>
                <Typography variant = "h6" align="left"><b>Part 3:</b> Additional Questions </Typography>
                </div>
                <div style={{width: "50%", marginBottom: "0px"}}>
                <FormControl>
                    <FormLabel id="demo-radio-buttons-group-label"> </FormLabel>
                    <RadioGroup
                        aria-labelledby="demo-radio-buttons-group-label"
                        name="radio-buttons-group"
                        onChange = {(event) => this.howRadioHandler(event)}
                        default={this.state.howChoice}
                        // TODO: Link this so that it resets for each example
                    >  
                    {knowledgeContent} 
                        
                    </RadioGroup>
                </FormControl>
                </div>
            </div>
            );
    }
}

export default RadioSliderPanel;